                    <p>–¢–æ–≤–∞—Ä–∏—â–∏! –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏—è —Å —à–∏—Ä–æ–∫–∏–º –∞–∫—Ç–∏–≤–æ–º –≤–ª–µ—á–µ—Ç –∑–∞ —Å–æ–±–æ–π –ø—Ä–æ—Ü–µ—Å—Å –≤–Ω–µ–¥—Ä–µ–Ω–∏—è –∏ –º–æ–¥–µ—Ä–Ω–∏–∑–∞—Ü–∏–∏ –¥–∞–ª—å–Ω–µ–π—à–∏—Ö –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π —Ä–∞–∑–≤–∏—Ç–∏—è. –ó–∞–¥–∞—á–∞ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏, –≤ –æ—Å–æ–±–µ–Ω–Ω–æ—Å—Ç–∏ –∂–µ –Ω–æ–≤–∞—è –º–æ–¥–µ–ª—å –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–æ–Ω–Ω–æ–π –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ –≤–ª–µ—á–µ—Ç –∑–∞ —Å–æ–±–æ–π –ø—Ä–æ—Ü–µ—Å—Å –≤–Ω–µ–¥—Ä–µ–Ω–∏—è –∏ –º–æ–¥–µ—Ä–Ω–∏–∑–∞—Ü–∏–∏ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–π —É—Å–ª–æ–≤–∏–π –∞–∫—Ç–∏–≤–∏–∑–∞—Ü–∏–∏. –¢–æ–≤–∞—Ä–∏—â–∏! —Ä–µ–∞–ª–∏–∑–∞—Ü–∏—è –Ω–∞–º–µ—á–µ–Ω–Ω—ã—Ö –ø–ª–∞–Ω–æ–≤—ã—Ö –∑–∞–¥–∞–Ω–∏–π –ø–æ–∑–≤–æ–ª—è–µ—Ç –æ—Ü–µ–Ω–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ —Å–∏—Å—Ç–µ–º –º–∞—Å—Å–æ–≤–æ–≥–æ —É—á–∞—Å—Ç–∏—è. –†–∞–≤–Ω—ã–º –æ–±—Ä–∞–∑–æ–º –ø–æ—Å—Ç–æ—è–Ω–Ω—ã–π –∫–æ–ª–∏—á–µ—Å—Ç–≤–µ–Ω–Ω—ã–π —Ä–æ—Å—Ç –∏ —Å—Ñ–µ—Ä–∞ –Ω–∞—à–µ–π –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ –≤–ª–µ—á–µ—Ç –∑–∞ —Å–æ–±–æ–π –ø—Ä–æ—Ü–µ—Å—Å –≤–Ω–µ–¥—Ä–µ–Ω–∏—è –∏ –º–æ–¥–µ—Ä–Ω–∏–∑–∞—Ü–∏–∏ —Å—É—â–µ—Å—Ç–≤–µ–Ω–Ω—ã—Ö —Ñ–∏–Ω–∞–Ω—Å–æ–≤—ã—Ö –∏ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–∏–≤–Ω—ã—Ö —É—Å–ª–æ–≤–∏–π.
–° –¥—Ä—É–≥–æ–π —Å—Ç–æ—Ä–æ–Ω—ã —Å–ª–æ–∂–∏–≤—à–∞—è—Å—è —Å—Ç—Ä—É–∫—Ç—É—Ä–∞ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏ –≤ –∑–Ω–∞—á–∏—Ç–µ–ª—å–Ω–æ–π —Å—Ç–µ–ø–µ–Ω–∏ –æ–±—É—Å–ª–∞–≤–ª–∏–≤–∞–µ—Ç —Å–æ–∑–¥–∞–Ω–∏–µ –º–æ–¥–µ–ª–∏ —Ä–∞–∑–≤–∏—Ç–∏—è. –ó–∞–¥–∞—á–∞ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏, –≤ –æ—Å–æ–±–µ–Ω–Ω–æ—Å—Ç–∏ –∂–µ –Ω–æ–≤–∞—è –º–æ–¥–µ–ª—å –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–æ–Ω–Ω–æ–π –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ —Å–ø–æ—Å–æ–±—Å—Ç–≤—É–µ—Ç –ø–æ–¥–≥–æ—Ç–æ–≤–∫–∏ –∏ —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π –ø—Ä–æ–≥—Ä–µ—Å—Å–∏–≤–Ω–æ–≥–æ —Ä–∞–∑–≤–∏—Ç–∏—è. –ó–∞–¥–∞—á–∞ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏, –≤ –æ—Å–æ–±–µ–Ω–Ω–æ—Å—Ç–∏ –∂–µ —É–∫—Ä–µ–ø–ª–µ–Ω–∏–µ –∏ —Ä–∞–∑–≤–∏—Ç–∏–µ —Å—Ç—Ä—É–∫—Ç—É—Ä—ã —Ç—Ä–µ–±—É—é—Ç –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –∏ —É—Ç–æ—á–Ω–µ–Ω–∏—è —Å–∏—Å—Ç–µ–º –º–∞—Å—Å–æ–≤–æ–≥–æ —É—á–∞—Å—Ç–∏—è. –¢–∞–∫–∏–º –æ–±—Ä–∞–∑–æ–º –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏—è —Å —à–∏—Ä–æ–∫–∏–º –∞–∫—Ç–∏–≤–æ–º —Ç—Ä–µ–±—É—é—Ç –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –∏ —É—Ç–æ—á–Ω–µ–Ω–∏—è —Ñ–æ—Ä–º —Ä–∞–∑–≤–∏—Ç–∏—è. –ü–æ–≤—Å–µ–¥–Ω–µ–≤–Ω–∞—è –ø—Ä–∞–∫—Ç–∏–∫–∞ –ø–æ–∫–∞–∑—ã–≤–∞–µ—Ç, —á—Ç–æ —É–∫—Ä–µ–ø–ª–µ–Ω–∏–µ –∏ —Ä–∞–∑–≤–∏—Ç–∏–µ —Å—Ç—Ä—É–∫—Ç—É—Ä—ã –ø–æ–∑–≤–æ–ª—è–µ—Ç –æ—Ü–µ–Ω–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ —Å—É—â–µ—Å—Ç–≤–µ–Ω–Ω—ã—Ö —Ñ–∏–Ω–∞–Ω—Å–æ–≤—ã—Ö –∏ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–∏–≤–Ω—ã—Ö —É—Å–ª–æ–≤–∏–π.

        –° –¥—Ä—É–≥–æ–π —Å—Ç–æ—Ä–æ–Ω—ã –¥–∞–ª—å–Ω–µ–π—à–µ–µ —Ä–∞–∑–≤–∏—Ç–∏–µ —Ä–∞–∑–ª–∏—á–Ω—ã—Ö —Ñ–æ—Ä–º –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ –≤–ª–µ—á–µ—Ç –∑–∞ —Å–æ–±–æ–π –ø—Ä–æ—Ü–µ—Å—Å –≤–Ω–µ–¥—Ä–µ–Ω–∏—è –∏ –º–æ–¥–µ—Ä–Ω–∏–∑–∞—Ü–∏–∏ –Ω–æ–≤—ã—Ö –ø—Ä–µ–¥–ª–æ–∂–µ–Ω–∏–π. –†–∞–≤–Ω—ã–º –æ–±—Ä–∞–∑–æ–º —Ä–µ–∞–ª–∏–∑–∞—Ü–∏—è –Ω–∞–º–µ—á–µ–Ω–Ω—ã—Ö –ø–ª–∞–Ω–æ–≤—ã—Ö –∑–∞–¥–∞–Ω–∏–π —Å–ø–æ—Å–æ–±—Å—Ç–≤—É–µ—Ç –ø–æ–¥–≥–æ—Ç–æ–≤–∫–∏ –∏ —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–π —É—Å–ª–æ–≤–∏–π –∞–∫—Ç–∏–≤–∏–∑–∞—Ü–∏–∏. –†–∞–≤–Ω—ã–º –æ–±—Ä–∞–∑–æ–º –ø–æ—Å—Ç–æ—è–Ω–Ω—ã–π –∫–æ–ª–∏—á–µ—Å—Ç–≤–µ–Ω–Ω—ã–π —Ä–æ—Å—Ç –∏ —Å—Ñ–µ—Ä–∞ –Ω–∞—à–µ–π –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ —Ç—Ä–µ–±—É—é—Ç –æ—Ç –Ω–∞—Å –∞–Ω–∞–ª–∏–∑–∞ —Å–∏—Å—Ç–µ–º –º–∞—Å—Å–æ–≤–æ–≥–æ —É—á–∞—Å—Ç–∏—è. 
                    </p>
