import React, { useState } from "react";
import classes from "./form.module.css";
import { useForm } from "react-hook-form";
import { sendQuestions } from "../../../redux/actions/sendQuestion/question";
import { useDispatch, useSelector } from "react-redux";

const Form = () => {
  const { handleSubmit, register, errors } = useForm();
  const dispatch = useDispatch();
  const onSubmit = (values) => dispatch(sendQuestions(values));
  return (
    <form className={classes.form} onSubmit={handleSubmit(onSubmit)} MutationObserverunregister shouldUnregister>
      <div className={classes.leftColumn}>
        <div className={classes.inputs}>
          <label>–§–ò–û*</label>
          <input
            type="text"
            className={classes.input}
            placeholder="–í–∞—à –§–ò–û"
            name="fio"
            ref={register({
              validate: (fio) => fio && fio.length > 4,
            })}
          />
          {errors.fio && (
            <span className={classes.error}>–ù–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –≤–µ–ª–ª–∏ –¥–∞–Ω–Ω—ã–µ</span>
          )}
        </div>
        <div className={classes.inputs}>
          <label>–ù–∞–∑–≤–∞–Ω–∏–µ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏</label>
          <input
            type="text"
            className={classes.input}
            placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏"
            name="name"
            ref={register({
              validate: (name) => name && name.length > 2,
            })}
          />
          {errors.name && (
            <span className={classes.error}>
              –ù–∞–∑–≤–∞–Ω–∏–µ –æ–≥—Ä–∞–Ω–∏–∑–∞—Ü–∏–∏ –º–µ–Ω—å—à–µ 2 —Å–∏–º–≤–æ–ª–æ–≤
            </span>
          )}
        </div>
        <div className={classes.inputs}>
          <label>–ü–æ—á—Ç–∞*</label>
          <input
            type="text"
            className={classes.input}
            placeholder="–í–∞—à email"
            name="email"
            ref={register({
              required: "–≠—Ç–æ –ø–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ",
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "–ù–µ–≤–µ—Ä–Ω—ã–π –∞–¥—Ä–µ—Å —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω–æ–π –ø–æ—á—Ç—ã",
              },
            })}
          />
          {errors.email && (
            <span className={classes.error}>{errors.email.message}</span>
          )}
        </div>
        <div className={classes.inputs}>
          <label>–¢–µ–ª–µ—Ñ–æ–Ω*</label>
          <input
            type="text"
            className={classes.input}
            placeholder="+996 ___ - __ - __ - __"
            name="phone"
            ref={register({
              validate: (phone) => phone && phone.length > 6,
            })}
          />
          {errors.phone && (
            <span className={classes.error}>
              –ù–∞–∑–≤–∞–Ω–∏–µ –æ–≥—Ä–∞–Ω–∏–∑–∞—Ü–∏–∏ –º–µ–Ω—å—à–µ 6 —Å–∏–º–≤–æ–ª–æ–≤
            </span>
          )}
        </div>
      </div>
      <div className={classes.rightColumn}>
        <div className={classes.inputs}>
          <label>–í–∞—à –≤–æ–ø—Ä–æ—Å</label>
          <textarea
            type="text"
            className={classes.inputTextarea}
            placeholder="–í–∞—à –≤–æ–ø—Ä–æ—Å"
            name="question"
            ref={register({
              required: "–≠—Ç–æ –ø–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ",
              pattern: {
                message: "–í–≤–µ–¥–∏—Ç–µ –≤–æ–ø—Ä–æ—Å",
              },
            })}
          />
          {errors.question && (
            <span className={classes.error}>{errors.question.message}</span>
          )}
        </div>
        <button type="submit" className={classes.btn}>
          –û—Ç–ø—Ä–∞–≤–∏—Ç—å
        </button>
      </div>
    </form>
  );
};
export default Form;
