import React, { useEffect } from 'react';
import st from './orderResearch.module.css';
import { useDispatch, useSelector  } from "react-redux";
import { orderResearch } from '../../redux/reducers/orderResearch/orderResearch';
import { useForm } from "react-hook-form";
import { orderResearchData } from '../../redux/actions/actions';


const OrderResearch = () =>{
    const { handleSubmit, register, errors } = useForm();
    const dispatch = useDispatch();
    const data = useSelector((state) => state.orderResearchData.data.data);
    useEffect(() => {
        dispatch(orderResearchData());
    }, []);
    const onSubmit = (data) =>{
        dispatch(orderResearch(data));
    }

    return(
        <div className={st.order_block}>
            <div className={st.order_block_anchor}>
                <img src={require('./target.png')} alt="img" />
                <div className={st.order_anchor_text}>
                    <h3>–ó–∞–∫–∞–∑–∞—Ç—å –∏—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ</h3>
                    <p>–ù–µ –Ω–∞—à–ª–∏ –Ω—É–∂–Ω–æ–µ –∏—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ? –ó–∞–∫–∞–∂–∏—Ç–µ –≤–∞—à–µ –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω–æ–µ –∏—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ —É –Ω–∞—Å</p>
                    <a href="#form">–ó–∞–∫–∞–∑–∞—Ç—å –∏—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ</a>
                </div>
            </div>
            <div className={st.order_instruct}>
                <img src={require('./Rectangle 55.png')} alt="img"></img>
                <div className={st.order_instruct_descrip}>
                    <h2>–ö—Ä–∞—Ç–∫–∞—è –∏–Ω—Å—Ç—Ä—É–∫—Ü–∏—è</h2>
                    <p>–ü–æ–≤—Å–µ–¥–Ω–µ–≤–Ω–∞—è –ø—Ä–∞–∫—Ç–∏–∫–∞ –ø–æ–∫–∞–∑—ã–≤–∞–µ—Ç, —á—Ç–æ –ø–æ—Å—Ç–æ—è–Ω–Ω—ã–π –∫–æ–ª–∏—á–µ—Å—Ç–≤–µ–Ω–Ω—ã–π —Ä–æ—Å—Ç –∏ —Å—Ñ–µ—Ä–∞ –Ω–∞—à–µ–π –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ –ø–æ–∑–≤–æ–ª—è–µ—Ç –≤—ã–ø–æ–ª–Ω—è—Ç—å –≤–∞–∂–Ω—ã–µ –∑–∞–¥–∞–Ω–∏—è –ø–æ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–µ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π –ø—Ä–æ–≥—Ä–µ—Å—Å–∏–≤–Ω–æ–≥–æ —Ä–∞–∑–≤–∏—Ç–∏—è. –ó–Ω–∞—á–∏–º–æ—Å—Ç—å —ç—Ç–∏—Ö –ø—Ä–æ–±–ª–µ–º –Ω–∞—Å—Ç–æ–ª—å–∫–æ –æ—á–µ–≤–∏–¥–Ω–∞, —á—Ç–æ –Ω–æ–≤–∞—è –º–æ–¥–µ–ª—å –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–æ–Ω–Ω–æ–π –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ –∏–≥—Ä–∞–µ—Ç –≤–∞–∂–Ω—É—é —Ä–æ–ª—å –≤ —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏–∏ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π –ø—Ä–æ–≥—Ä–µ—Å—Å–∏–≤–Ω–æ–≥–æ —Ä–∞–∑–≤–∏—Ç–∏—è.
                    </p>
                </div>
                <div className={st.order_instruct_descrip}>
                    <h2>–ö—Ä–∞—Ç–∫–∞—è –∏–Ω—Å—Ç—Ä—É–∫—Ü–∏—è</h2>
                    <p>–ü–æ–≤—Å–µ–¥–Ω–µ–≤–Ω–∞—è –ø—Ä–∞–∫—Ç–∏–∫–∞ –ø–æ–∫–∞–∑—ã–≤–∞–µ—Ç, —á—Ç–æ –ø–æ—Å—Ç–æ—è–Ω–Ω—ã–π –∫–æ–ª–∏—á–µ—Å—Ç–≤–µ–Ω–Ω—ã–π —Ä–æ—Å—Ç –∏ —Å—Ñ–µ—Ä–∞ –Ω–∞—à–µ–π –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ –ø–æ–∑–≤–æ–ª—è–µ—Ç –≤—ã–ø–æ–ª–Ω—è—Ç—å –≤–∞–∂–Ω—ã–µ –∑–∞–¥–∞–Ω–∏—è –ø–æ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–µ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π –ø—Ä–æ–≥—Ä–µ—Å—Å–∏–≤–Ω–æ–≥–æ —Ä–∞–∑–≤–∏—Ç–∏—è. –ó–Ω–∞—á–∏–º–æ—Å—Ç—å —ç—Ç–∏—Ö –ø—Ä–æ–±–ª–µ–º –Ω–∞—Å—Ç–æ–ª—å–∫–æ –æ—á–µ–≤–∏–¥–Ω–∞, —á—Ç–æ –Ω–æ–≤–∞—è –º–æ–¥–µ–ª—å –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–æ–Ω–Ω–æ–π –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ –∏–≥—Ä–∞–µ—Ç –≤–∞–∂–Ω—É—é —Ä–æ–ª—å –≤ —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏–∏ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π –ø—Ä–æ–≥—Ä–µ—Å—Å–∏–≤–Ω–æ–≥–æ —Ä–∞–∑–≤–∏—Ç–∏—è.
                    </p>
                </div>
                <img src={require('./Rectangle 55.png')}></img>
            </div>
            <div className={st.order_form} id="form">
                <h3>–ó–∞—è–≤–∫–∞ –Ω–∞ –∏—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ</h3>
                <p>–û—Å—Ç–∞–≤—å—Ç–µ —Å–≤–æ–∏ –∫–æ–Ω—Ç–∞–∫—Ç–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∏ –º—ã –ø—Ä–∏—à–ª–µ–º –≤–∞–º –Ω–∞ –ø–æ—á—Ç—É –∞–Ω–∫–µ—Ç—É, –∑–∞–ø–æ–ª–Ω–∏–≤ –∫–æ—Ç–æ—Ä—É—é, –≤—ã —Å–º–æ–∂–µ—Ç–µ –∑–∞–∫–∞–∑–∞—Ç—å –∏—Å—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ</p>
                <form>
                    <div className={st.form_input_block}>
                        <label for="name">–ò–º—è*</label>
                        <input id="name" placeholder="–í–∞—à–µ –∏–º—è" name="name" ref={register({
                        validate: (name) => name && name.length > 4,
                        })}></input>
                        {errors.name && (
                            <span className={st.errorString}>–ù–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –≤–≤–µ–ª–∏ –¥–∞–Ω–Ω—ã–µ</span>
                        )}
                        <label for="surname">–§–∞–º–∏–ª–∏—è*</label>
                        <input id="surname" placeholder="–í–∞—à–∞ —Ñ–∞–º–∏–ª–∏—è" name="surname" ref={register({
                        validate: (surname) => surname && surname.length > 4,
                        })}></input>
                        {errors.surname && (
                            <span className={st.errorString}>–ù–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –≤–≤–µ–ª–∏ –¥–∞–Ω–Ω—ã–µ</span>
                        )}
                        <label for="companyName">–ù–∞–∑–≤–∞–Ω–∏–µ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏</label>
                        <input id="companyName" placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏" name="logo" ref={register({
                        validate: (logo) => logo && logo.length > 4,
                        })}></input>
                         {errors.logo && (
                            <span className={st.errorString}>–ó–∞–ø–æ–ª–Ω–∏—Ç–µ —ç—Ç–æ –ø–æ–ª–µ</span>
                        )}
                        <label for="mail">–ü–æ—á—Ç–∞*</label>
                        <input id="mail" placeholder="–í–∞—à email" name="email" ref={register({
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                            message: "–ù–µ–≤–µ—Ä–Ω—ã–π –∞–¥—Ä–µ—Å —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω–æ–π –ø–æ—á—Ç—ã",
                          }
                        })}></input>
                        {errors.email && (
                            <span className={st.errorString}>{errors.email.message}</span>
                        )}
                        <label for="phone_number">–¢–µ–ª–µ—Ñ–æ–Ω*</label>
                        <input id="phone_number" placeholder="+996___-__-__-__" name="phone_number" ref={register({
                        validate: (phone_number) => phone_number && phone_number > 6
                        })}></input>
                        {errors.phone_number && (
                            <span className={st.errorString}>–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –ø–æ–ª–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ</span>
                        )}
                    </div>
                    <div className={st.form_textarea_block}>
                        <label for="description">–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è*</label>
                        <textarea id="description" placeholder="–ö—Ä–∞—Ç–∫–æ–µ –æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–¥—É–∫—Ç–∞\—É—Å–ª—É–≥–∏, —Ä—ã–Ω–∫–∞, –æ—Ç—Ä–∞—Å–ª–∏, –≥–µ–æ–≥—Ä–∞—Ñ–∏–∏ –æ—Ö–≤–∞—Ç–∞" name="description" ref={register({
                        validate: (description) => description && description.length > 10
                        })}></textarea>
                        {errors.description && (
                            <span className={st.errorString}>–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –ø–æ–ª–µ</span>
                        )}
                    </div>
                    <button onClick={handleSubmit(onSubmit)}>–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>
                </form>
            </div>
        </div>
    )

}

export default OrderResearch;