import React, {useEffect} from 'react';
import st from './bePartner.module.css';
import { bePartner } from '../../redux/reducers/bePartner/bePartner';
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";
import { BePartnerData } from '../../redux/actions/actions';

const BePartner = () =>{

    const dispatch = useDispatch();
    const data = useSelector((state) => state.BePartnerData.data.data);
        useEffect(() => {
        dispatch(BePartnerData());
    }, []);

    const { handleSubmit, register, errors } = useForm();
    const onSubmit = (data) =>{
        dispatch(bePartner(data));
    }


    return(
        <div className={st.be_partner}>
            <img src={require('./bePartner.png')} alt="img"></img>
            <div className={st.be_partner_container}>
                {data &&(data.map(elem =>(
                    <div>
                        <h2>{elem.header}</h2>
                        <p>{elem.description}</p>
                    </div>
                )))}
            </div>
            <div className={st.order_form}>
                <h3>–û–±—Ä–∞—Ç–Ω–∞—è —Å–≤—è–∑—å</h3>
                <form>
                    <div className={st.form_input_block}>
                        <label for="name">–§–ò–û*</label>
                        <input id="name" placeholder="–í–∞—à –§.–ò.–û" name="name" ref={register({
                        validate: (name) => name && name.length > 4,
                        })}></input>
                        {errors.name && (
                            <span className={st.errorString}>–ù–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –≤–≤–µ–ª–∏ –¥–∞–Ω–Ω—ã–µ</span>
                        )}
                        <label for="name_of_organization">–ù–∞–∑–≤–∞–Ω–∏–µ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏</label>
                        <input id="name_of_organization" placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ü–∏–∏" name="name_of_organization" ref={register({
                        validate: (name_of_organization) => name_of_organization && name_of_organization.length > 2,
                        })}></input>
                        {errors.name_of_organization && (
                            <span className={st.errorString}>–ù–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –≤–≤–µ–ª–∏ –¥–∞–Ω–Ω—ã–µ</span>
                        )}
                        <label for="position">–î–æ–ª–∂–Ω–æ—Å—Ç—å</label>
                        <input id="position" placeholder="–î–æ–ª–∂–Ω–æ—Å—Ç—å" name="position" ref={register({
                        validate: (position) => position && position.length > 2,
                        })}></input>
                        {errors.position && (
                            <span className={st.errorString}>–ù–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –≤–≤–µ–ª–∏ –¥–∞–Ω–Ω—ã–µ</span>
                        )}
                        <label for="mail">–ü–æ—á—Ç–∞*</label>
                        <input id="mail" placeholder="–í–∞—à email" name="email" ref={register({
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                            message: "–ù–µ–≤–µ—Ä–Ω—ã–π –∞–¥—Ä–µ—Å —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω–æ–π –ø–æ—á—Ç—ã",
                          }
                        })}></input>
                        {errors.email && (
                            <span className={st.errorString}>{errors.email.message}</span>
                        )}
                        <label for="phone">–¢–µ–ª–µ—Ñ–æ–Ω*</label>
                        <input id="phone" placeholder="+996___-__-__-__"  name="phone" ref={register({
                        validate: (phone) => phone && phone > 6
                        })}></input>
                        {errors.phone && (
                            <span className={st.errorString}>–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –ø–æ–ª–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ</span>
                        )}
                    </div>
                    <div className={st.form_textarea_block}>
                        <label for="description">–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è*</label>
                        <textarea id="description" placeholder="–î–æ–ø–æ–ª—å–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è"  name="extra" ref={register({
                        validate: (extra) => extra && extra.length > 10
                        })}></textarea>
                        {errors.extra && (
                            <span className={st.errorString}>–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –ø–æ–ª–µ</span>
                        )}
                        <button onClick={handleSubmit(onSubmit)}>–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default BePartner;