import React from 'react';
import st from './recoveryPassword.module.css';
import { Link } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import { recoveryPassword } from '../../redux/actions/actions';

const RecoveryPassword = () =>{
    const { handleSubmit, register, errors } = useForm();
    const dispatch = useDispatch();
    const onSubmit = (data) =>{
        dispatch(recoveryPassword(data));
    }
    return(
        <div className={st.recoveryBlock}>
            <div className={st.recoveryBlock_content}>
                <Link to = '/' className = {st.strikeLink} >
                    <div className={st.strike}></div>
                </Link>
                <h2>–í–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–∞—Ä–æ–ª—è</h2>
                <form>
                    <label>E-mail</label>
                    <input placeholder="–í–∞—à e-mail" name="email" ref={register({
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                          },
                        validate: (email) => email && email.length > 4
                        })}></input>
                     {errors.email && (
                            <span className={st.errorString}>–ù–µ–≤–µ—Ä–Ω—ã–π –∞–¥—Ä–µ—Å —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω–æ–π –ø–æ—á—Ç—ã</span>
                        )}
                    <button onClick={handleSubmit(onSubmit)}>–û—Ç–ø—Ä–∞–≤–∏—Ç—å –ø–∏—Å—å–º–æ</button>
                </form>
            </div>
        </div>
    )
}

export default RecoveryPassword;